body {
    background-color: cadetblue;
}

.MessageContainer {
    display: flex;
    align-items: flex-end;
    margin: 10px;
}

.avatar {
    background-image: url("https://sun9-74.userapi.com/Ph-WiuOtF985il9AvN9JqiCWedmHtSGSSTXrSA/ltEB2Z2-YO4.jpg");

}

.imgAvatar {
    border-radius: 50%;
    width: 60px;
    height: 60px;
}

.textContainer {
    padding: 10px;
    border-radius: 5px 5px 5px 0;
    background-color: rgba(255, 255, 255 , 0.3);
}

.messageDiv {
    min-width: 150px;
    max-width: 250px;
    word-wrap: break-word;
}

.name {
    font-weight: bold;
}

.time {
    display: flex;
    justify-content: flex-end;
}